@import url(https://fonts.googleapis.com/css?family=Crete+Round);
@import url(https://fonts.googleapis.com/css?family=Pacifico);
#category-cirle {
  background-color: #1f2326;
  color: white;
  padding: 16px;
  font-size: 16px;
  height: 25px;
  width: 25px;
  border: none;
  border-radius: 50%;
  -webkit-transition: 100ms;
  -o-transition: 100ms;
  transition: 100ms;
  font-family: 'Crete Round';
}
#dropdown {
  position: relative;
  display: inline-block;
  margin-right: 2%;
  margin-left: 2%;
}
#dropdown-content{
  display: none;
  position: absolute;
  background-color: #1f2326;
  min-width: 160px;
  -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
          box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 3;
}
#dropdown-content a {
  color: #3FB8FF;
  text-align: center;
  text-decoration: none;
  display: block;
  cursor: default;
}
ul, li{
  list-style-type: none;
}
#dropdown-content a:hover{
  color: #ffffff;
  text-decoration: none;
}

#dropdown:hover #category-cirle {
  opacity: 0;
}
#dropdown:hover #dropdown-content{
  display: block;
}
#dropdown:hover #category-circle{
  background-color: #0C85D3;
}
#dropdownContainer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 2%;
}
#mainContainerParent{
  top: 15%;
  position: fixed;
  display: inline-block;
  /* display: inline-flex;
  justify-content: center; */
  width: 100%;
}
#mainContainer{
  max-width: 500px;
  width: 95%;
  margin: 0 auto;
  position: relative;
  border-radius: 2px;
  -webkit-transition: opacity .8s ease .1s;
  -o-transition: opacity .8s ease .1s;
  transition: opacity .8s ease .1s;
}
#search-bar-container{
  width: 95%;
  max-width: 500px;
  margin: 0 auto;
  position: relative;
  border-radius: 2px;
  background-color: #1f2326;
  font-size: 9pt;
}

#search-bar{
  background: 0 0;
  font-size: 1.76em;
  text-align: center;
  color: #7291a1;
  padding: 6px 0;
  display: block;
  width: 100%;
  height: 30px;
  -webkit-transition: 5s;
  -o-transition: 5s;
  transition: 5s;
  border: 0;
  margin-left: auto;
  margin-right: auto;
  -webkit-transition: .2s cubic-bezier(.4,0,.2,1);
  -o-transition: .2s cubic-bezier(.4,0,.2,1);
  transition: .2s cubic-bezier(.4,0,.2,1);
  font-family: product sans,Arial,sans-serif;
}
#search-bar:focus{
  color: whitesmoke;
}
/* #search-bar:focus ~ .search-info-desc{
  color: red;
} */

html, body{
  margin: 0;
  background-color: black;
}
#root{
  width:100%;
  height:100vh;
  background-size:cover;
  overflow:hidden;
}
#body{
  height: inherit;
  width: inherit;
  font-family: roboto,sans-serif;
}
#myCanvas{
  height: inherit;
  background-size:cover;
  overflow:hidden;
  width: inherit;
  z-index: 1;
}

#search-info-container{
  margin-top: 8px;
  -webkit-transition: .2s cubic-bezier(.4,0,.2,1) .2s;
  -o-transition: .2s cubic-bezier(.4,0,.2,1) .2s;
  transition: .2s cubic-bezier(.4,0,.2,1) .2s;
  max-width: 500px;
  width: 100%;
  margin: 0 auto;
  position: relative;
  /* display: flex; */
  display: none;
  -ms-flex-pack: center;
      justify-content: center;
}
#search-info-list{
  width: 95%;
  padding-left: 0;
}
.search-info-desc{
  background-color: #1f2326;
  padding: 4px 0;
  margin-bottom: 8px;
  color: #a7b3b9;
  overflow: hidden;
  height: auto;
  -webkit-transition: .1s cubic-bezier(.4,0,.2,1) .1s;
  -o-transition: .1s cubic-bezier(.4,0,.2,1) .1s;
  transition: .1s cubic-bezier(.4,0,.2,1) .1s;
  opacity: 0;
  margin-left: 100px;
  margin-left: auto;
  margin-right: auto;
}
#search-info-list span{
  margin-right: 10px;
  padding: 10px;
  width: 90px;
  background-color: #1a1d1f;
}
.datetime-text{
  color: white;
  padding: -2px;
  font-size: 0.8em;
  text-align: right;
}
#datetime-text-holder{
  width: 5%;
  z-index: 3;
  position: fixed;
  right: 0.5%;
}
