  position: fixed;
  z-index: 3;
  color: whitesmoke;
  font-size: 4em;
  margin-top: -6%;
  opacity: 1;
  font-family: 'Pacifico';
} */

#animationHolder{
  position: absolute;
  left:0; 
  top:-70%; 
  width:100%; 
  height:100%
}

#animationText{
  stroke: #0C85D3;
  stroke-width: 1;
  font-size: 4.05em;
  /* font-size: 3vw; */
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  top:50%;
}



